t: center;\n  width: 100px;\n}\n\n/* dialog form */\nform.normal-form {\n  margin: 10px;\n}\n\n.controles-container {\n  width: 100%;\n  padding: 5%;\n}\n\n.controles-container > * {\n  width: 100%;\n}\n\n.add-bottom-padding {\n  padding-bottom: 10px;\n}\n\n.textarea-container mat-form-field {\n  margin: 5px;\n  height: 100px;\n  padding-bottom: 10px;\n}\n\n.button-row button {\n  margin: 5px;\n}\n\n/* notification */\nsnack-bar-container.success {\n  background-color: #5cb85c;\n  color: #fff;\n}\n\nsnack-bar-container.warn {\n  background-color: #f99157;\n  color: #fff;\n}\n\nsnack-bar-container.notification simple-snack-bar {\n  font-size: 14px !important;\n  font-weight: bold;\n}\n\nmat-footer-row mat-footer-cell {\n  justify-content: center;\n  font-style: italic;\n}\n\n/* filter controls */\n.search-div {\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  flex-flow: row nowrap;\n}\n\n.search-form-field {\n  width: 60%;\n  margin-left: 10px;\n  padding: 5px 10px;\n  border-radius: 5px;\n}\n\n.create {\n  height: 40px;\n  width: 180px;\n}\n\n/* for dialog pop-up */\n.btn-dialog-close {\n  width: 45px;\n  min-width: 0px !important;\n  height: 40px;\n  padding: 0px !important;\n}\n\n.s-card {\n  transition: all linear 300ms !important;\n  max-height: 600px;\n  background-color: lightgoldenrodyellow;\n}\n\n.s-card.ng-enter {\n  animated: fadeInDown 400ms;\n}\n\n.s-card:hover {\n  box-shadow: 0 3px 4px -2px rgba(0, 0, 0, 0.2), 0 7px 5px 1px rgba(0, 0, 0, 0.1), 0 3px 12px 2px rgba(0, 0, 0, 0.19);\n  transform: translate3d(0, -0.5em, 0);\n  -webkit-transform: translate3d(0, -0.5em, 0);\n  -moz-transform: translate3d(0, -0.5em, 0);\n  -ms-transform: translate3d(0, -0.5em, 0);\n  -o-transform: translate3d(0, -0.5em, 0);\n}\n\n.container {\n  display: flex;\n  justify-content: space-between;\n  flex-flow: row nowrap;\n}\n\n.panelLeft {\n  justify-content: flex-start;\n  flex: 1 1 50%;\n}\n\n.panelRight {\n  padding: 5px;\n  fle